  flex: 1; /* additionally, equal width */


}

.imgContainer{
  display: inline-block;
  width: 70%;
  max-width: 320px;
}

.passimg {
  width: 30%;
  max-width: 100px;
  padding: 3px;
}

.passimg:hover {
  background-color: #EEEEEE;
}

.selectedimg {
  background-color: #7EDA9D;
  width: 30%;
  max-width: 100px;
  padding: 3px;

}

